import { Col, Form, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { StoredManageService } from "../../../services/adminService";
import { Fn } from "../../../helper/function";
import { MyButton } from "../../core/MyButton";
import ManageStoredLinkViewComponent from "./Link_ViewComponent";
import StoredmanageHistoryBoxComponent from "./HistoryBoxComponent";
import { useNavigate, useParams } from "react-router-dom";
import moment from "moment";
import { MyFormItem } from "../../core/Input";
import { MyBox } from "../../core/MyBox";

export default function ManageStoredManageDetailComponent() {
    let key: string | undefined = useParams().key;
    let navigate = useNavigate();

    const options = [
        {
            key: 0,
            label: "Developing",
            value: 0
        },
        {
            key: 1,
            label: "Đã deploy",
            value: 1
        },
        {
            key: 2,
            label: "Đang fix",
            value: 2
        },
        {
            key: -1,
            label: "Đã xóa",
            value: -1
        }
    ]

    const [name, setName] = useState('');
    const [status, setStatus] = useState(0);
    const [dateDeployDev, setDateDeployDev] = useState(moment());
    const [dateDeployProduct, setDateDeployProduct] = useState(moment());
    const [histories] = useState([]);
    const [note, setNote] = useState('');
    const [listViewData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    const loadData = useCallback(() => {
        if (key === undefined) {
            return;
        }

        setIsLoading(true);
        // StoredManageService.GetDetail(key)
        //     .then((data: any) => {
        //         let row = data.data.data;
        //         setName(row.name);
        //         setStatus(row.status);
        //         if (row.dateDeployDev != null) {
        //             setDateDeployDev(moment(row.dateDeployDev));
        //         }
        //         if (row.dateDeployProduct != null) {
        //             setDateDeployProduct(moment(row.dateDeployProduct));
        //         }
        //         setHistories(row.histories);

        //         StoredManageService.GetLinkStored(key, null)
        //             .then((data: any) => {
        //                 const row = data.data.data.pagedData.map((data: any, index: number) => {
        //                     return {
        //                         key: data.key,
        //                         value: data.storedDest.name
        //                     }
        //                 })
        //                 setListViewData(row);
        //             })
        //     })
        //     .finally(() => {
        //         setIsLoading(false);
        //     })
    }, [key, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function loadHistory() {
        return histories.map((data, index) => {
            return (
                <StoredmanageHistoryBoxComponent key={index} data={data} />
            )
        });
    }

    function handleClickSubmitForm() {
        setIsLoading(true);
        let body = {
            name: name,
            status: status,
            dateDeployDev: dateDeployDev,
            dateDeployProduct: dateDeployProduct,
            note: note
        };
        if (key !== undefined) {
            edit(body);
        } else {
            add(body);
        }
    }

    function add(body: any) {
        StoredManageService.Add(body, (data: any) => {
            Fn.PrintMsgSuccess_no_use(data);
            navigate(`/admin/storedManage/edit/${data.data.data.key}`);
        }, undefined, () => {
            setIsLoading(false);
        })
    }

    function edit(body: any) {
        StoredManageService.GetDetail(key, (data: any) => {
            body = Fn.GetTimestamp_no_use(data, body);
            StoredManageService.Edit(key, body, (data: any) => {
                Fn.PrintMsgSuccess_no_use(data);
                loadData();
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }

    return (
        <MyBox
            title={key === undefined ? "Thêm mới stored procedure" : "Chỉnh sửa stored procedure"}
            isLoading={isLoading}>
            <Form layout="vertical">
                <Row gutter={12}>
                    <Col span={12}>
                        <MyFormItem.Input
                            label="Tên stored"
                            value={name}
                            setValue={setName} />
                    </Col>
                    <Col span={12}>
                        <MyFormItem.Select
                            label="Trạng thái"
                            options={options}
                            value={status}
                            setValue={setStatus} />
                    </Col>
                    <Col span={12}>
                        <MyFormItem.DatePicker
                            label="Ngày deploy môi trường Dev"
                            value={dateDeployDev}
                            setValue={setDateDeployDev} />
                    </Col>
                    <Col span={12}>
                        <MyFormItem.DatePicker
                            label="Ngày deploy môi trường Production"
                            value={dateDeployProduct}
                            setValue={setDateDeployProduct} />
                    </Col>
                    <Col span={24}>
                        <MyFormItem.TextArea
                            label="Ghi chú"
                            value={note}
                            setValue={setNote} />
                    </Col>
                    <Col span={24}>
                        <MyButton
                            value={key === undefined ? "Thêm mới" : "Chỉnh sửa"}
                            type="success"
                            onClick={handleClickSubmitForm} />
                    </Col>
                    <Col span={12}>
                        <Form.Item label="Lịch sử thay đổi">
                            {loadHistory()}
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Form.Item label="Liên kết stored procedure khác">
                            <ManageStoredLinkViewComponent keyData={key} dataSource={listViewData} />
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </MyBox>
    );
}