import { useEffect, useState } from "react"

export default function ManageStoredLinkViewComponent(props: any) {
    const [data, setData] = useState([]);
    const { handleDelete } = props;
    useEffect(() => {
        const row = props.dataSource.map((data: any, index: number) => {
            return (
                <li key={data.key}>
                    {data.value}
                    <hr></hr>
                </li>
            )
        })
        setData(row);
    }, [props.dataSource, handleDelete]);

    return (
        <ul>
            {data}
        </ul>
    )
}