import { UIFormatDateTimeVNComponent } from "../../core/UI_FormatComponent";

export default function StoredmanageHistoryBoxComponent(props: any) {
    let data = props.data;

    function loadContent(data: any) {
        return data.map((data: any, index: number) => {
            return (
                <span key={index}>{data}<br /></span>
            )
        })
    }

    return (
        <div>
            <p>
                <strong>
                    <UIFormatDateTimeVNComponent value={data.date} />
                </strong>
            </p>
            {loadContent(data.contents)}
            <hr />
        </div>
    );
}